.App {
  text-align: center;
  max-width: 600px;
  margin: 0 auto;
  position: relative;
}

h1 {
  font-size: 2rem;
  margin: 15px 0 20px;
}

h2 {
  margin-bottom: 20px;
}

.container {
  display: flex;
  margin: 10px 20px 0 20px;
  justify-content: space-between;
}

