.header {
  position: sticky;
  top: 44px;
  background-color: #56728f;
  width: 100%;
  height: 115px;
}

.body {
  padding-top: 0;
}

.active {
  color: lightyellow;
  text-decoration: none;
}

a {
  color: rgb(145, 223, 248);
  font-size: 18px;
  text-decoration: none;
}

nav a {
  margin: 15px 10px;
}

a:hover {
  text-decoration: underline;
}

nav {
  text-align: left;
  padding: 10px;
  background-color: rgb(48, 104, 104);
  position: sticky;
  top: 0;
}

